<template>
    <div class="number-guess-page">
        <!-- 头 -->
        <van-nav-bar title="" left-arrow :border="false" @click-left="goBack"/>
        <!-- 玩法规则 -->
		<img class="go-rule" src="../assets/imgs/go-rule-draw.png" alt="" @click="goRulePage">
    </div>
</template>


<script>

import { mapState } from 'vuex'
import { mapMutations } from 'vuex'

import { Dialog  } from 'vant';

export default {
    data(){
        return{

        }
    },
    computed:{
        ...mapState(['user_info']),
    },
    created(){
        this.utils.info('user_info...',this.user_info);
        if(this.$route.params.id){
			this.utils.info('有id...已登录...');
            this.user_info.user_id = this.$route.params.id;
            
            //获取用户信息
		    this.getUserInfo('1');
		}else{
			this.utils.info('没有id...未登录...');
            // this.user_info.user_id = 1;
            
            Dialog.alert({
				title: '系统提示',
				message: '请先登录...'
			}).then(() => {
				// on close
				this.utils.info('返回app...');
				this.$bridge.callhandler('goBack', {tag:"返回app"}, (data) => {})
				
			});
        }
        this.utils.info('user_info...',this.user_info);
    },
    mounted(){

    },
    methods:{
        ...mapMutations(['changeUserInfo']),
        //返回
		goBack(){
			this.utils.info('返回app...');
			this.$bridge.callhandler('goBack', {tag:"返回app"}, (data) => {})
        },
        //玩法规则
		goRulePage(){
			this.$router.push({
				name:'numberGuessRule'
			})
        },
        //开奖记录
        goListPage(){
            this.$router.push({
				name:'numberGuessList'
			})
        },
        //我的战绩
		goMore(){
			this.utils.info('战绩更多');
			this.$bridge.callhandler('goMore', {tag:"战绩更多"}, (data) => {})
        },
        //获取用户信息 type 1 去请求奖品列表和用户列表 2不去
		getUserInfo(type){
            let user_id = 'dy' + this.user_info.user_id + this.utils.randomNumber(4);
			this.http.post(this.http._HOST + 'login/user_detail',{user_id:user_id}).then(result =>{
                this.utils.info('获取用户信息...',result);
                if(result.success){
                    let data = result.data;

                    this.user_info.dw_money = data.dw_money;
                    this.user_info.pay_password = data.pay_password;
                    this.user_info.token = data.token;
                    this.user_info.free_password = data.free_password;

                    if(type == '1'){
                        //获取奖品列表
                        //this.getPrizeList();

                        //获取中奖用户列表
                        //this.getWinList();
                    }
                    
                }
			})
        },
    }
}
</script>


<style lang="scss" scoped>
    .number-guess-page{
        width:100%;
        height:100%;
        position: relative;
		background: url(../assets/imgs/number-guess-bg.png) no-repeat center top;
		background-repeat: no-repeat;
        background-size: 100%;
    }
    .van-nav-bar{
		background: transparent;
	}
	.van-nav-bar .van-icon{
		font-size:20px;
		color:#fff;
	}
    /* 玩法规则 */
	.go-rule{
		position: absolute;
		width:85px;
		right:0;
		top:100px;
	}
</style>


